<template>
  <v-dialog
      v-model="model['show']"
      persistent
      max-width="400px"
  >
    <v-card>
      <v-card-title>
        <p class="overline">New directory</p>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-row>
            <v-col cols="12">
              <v-text-field
                  v-model="inputModel"
                  class="text"
                  label="Directory name"
                  required
                  outlined
              ></v-text-field>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
            color="orange darken-3"
            text
            @click="model['show'] = false"
        >
          Close
        </v-btn>
        <v-btn
            color="orange darken-3"
            text
            @click="onDoneClick()"
        >
          Save
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import UUID from '@/util/UUID'
export default {
  name: "EntryDirectoryModal",
  data() {
    return {
      inputModel: ''
    }
  },
  props: {
    value: {
      required: true
    }
  },
  computed: {
    model: {
      get () {
        return this.value
      },
      set (value) {
        this.$emit('input', value)
      }
    }
  },
  methods: {
    onDoneClick: function () {
      this.$store.dispatch('dashboard/insertDirectory', {
        data: {
          id: UUID.generate(),
          isDirectory: true,
          name: this.inputModel,
          parentId: this.model['currentDirId']
        },
        callback: () => {
          this.model['show'] = false
          this.model['onSuccess']()
        },
        error: (error) => {
          this.model['onError'](error)
        }
      })
    }
  }
}
</script>

<style scoped>
.overline {
  font-family: 'Raleway', sans-serif !important;
  font-size: 18px !important;
}
.text {
  font-family: 'Raleway', sans-serif;
}
</style>