<template>
  <v-container>
    <v-card
        class="my-5"
        elevation="0"
        v-if="this.model['show']"
    >
      <v-card-text class="text-center">
        <v-img
            class="mb-2 mx-auto"
            height="120"
            width="120"
            :src="require('../assets/empty_state.png')"/>

        <h1 class="mb-5 .h1">Oops</h1>
        <p class="text">Seems that there are no {{ this.model['dirOrEntry'] }} here yet. Create new one now :)!</p>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
export default {
  name: "EmptyState",
  props: {
    value: {
      required: true
    }
  },
  computed: {
    model: {
      get() {
        return this.value
      },
      set(value) {
        this.$emit('input', value)
      }
    }
  },
}
</script>

<style scoped>
.h1 {
  font-family: 'Raleway', sans-serif;
}

.text {
  font-family: 'Raleway', sans-serif;
  font-size: large;
}
</style>