
const SIMPLE_UNIVERSE =
	"abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNNOPQRSTUVWXYZ0123456789-_+;:*()=$&¡!@#"
const COMPLEX_UNIVERSE = `${SIMPLE_UNIVERSE}àèìòùáéíóúäëïöüçÀÈÌÒÙÁÉÍÓÚÄËÏÖÜÇ.,{}[]'/\\%<>`

const randNum = function (maxInclusive) {
	return Math.floor(Math.random() * (maxInclusive + 1))
}

const generateUniverse = function (isSimple) {
	let universe = isSimple ? SIMPLE_UNIVERSE.split('') : COMPLEX_UNIVERSE.split('')
	const result = []
	for (let i = 0 ; i < universe.length ; i++) {
		result.push(universe.splice(randNum(universe.length), 1)[0])
	}
	return result
}

const generate = function (isSimple, length) {
	const universe = generateUniverse(isSimple)
	const result = []
	for (let i = 0 ; i < length ; i++) {
		result.push(universe.splice(randNum(universe.length), 1)[0])
	}
	return result.join('')
}

export {
	generate
}