<template>
  <v-dialog
      v-model="model['show']"
      persistent
      max-width="400px"
  >
    <v-card>
      <v-card-title>
        <p class="overline">Rename directory:</p>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-row>
            <v-col cols="12">
              <v-text-field
                  v-model="model['name']"
                  class="text"
                  label="Directory name"
                  :rules="[rules.required]"
                  required
                  outlined
              >

              </v-text-field>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
            color="orange darken-3"
            text
            @click="onCancel()"
        >
          Close
        </v-btn>
        <v-btn
            color="orange darken-3"
            text
            @click="onDoneClick()"
        >
          Save
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "RenameModal",
  data() {
    return {
      rules: {
        required: (value) => (value || '').length > 0 || 'Required field!'
      }
    }
  },
  props: {
    value: {
      required: true
    }
  },
  computed: {
    model: {
      get() {
        return this.value
      },
      set(value) {
        this.$emit('input', value)
      }
    }
  },
  methods: {
    onDoneClick: function () {
      this.$store.dispatch('dashboard/updateStoredDirName', {
        id: this.model['selectedItem']['id'],
        name: this.model['name']
      })
      this.$store.dispatch('dashboard/updateDirectory', {
        data: {
          id: this.model['selectedItem']['id']
        },
        callback: () => {
          this.model['show'] = false
        },
        error: (error) => {
          this.model['onError'](error)
        }
      })
    },
    onCancel: function() {
      this.model['show'] = false
      this.model['onCancel']()
    }
  }
}
</script>

<style scoped>
.overline {
  font-family: 'Raleway', sans-serif !important;
  font-size: 18px !important;
}

.text {
  font-family: 'Raleway', sans-serif;
}
</style>