import * as crypto from 'crypto'

const algorithm = 'aes-256-cbc'
const secret = "mCfEdvfMxosvMPZHy5XF++vkCaU3aPk/J1affdT2pBs="

const encrypt = (data) => {
	let key = crypto.randomBytes(32);
	let ivRaw = crypto.randomBytes(16);
	let ivSecret = crypto.randomBytes(16);

	let cipherRaw = crypto.createCipheriv(algorithm, Buffer.from(key), ivRaw);
	let encryptedRaw = cipherRaw.update(JSON.stringify(data))
	encryptedRaw = Buffer.concat([encryptedRaw, cipherRaw.final()]);
	let cipherSecret = crypto.createCipheriv(algorithm, new Buffer(Buffer.from(secret, 'base64')), ivSecret)
	let encryptedSecret = cipherSecret.update(Buffer.from(key))
	encryptedSecret = Buffer.concat([encryptedSecret, cipherSecret.final()]);
	return {
		ivRaw: ivRaw.toString('base64'),
		ivSecret: ivSecret.toString('base64'),
		secret: encryptedSecret.toString('base64'),
		raw: encryptedRaw.toString('base64')
	}
}

const decrypt = (cryptoObject) => {
	let ivSecret = Buffer.from(cryptoObject['ivSecret'], 'base64')
	let ivRaw = Buffer.from(cryptoObject['ivRaw'], 'base64')
	let decipherSecret = crypto.createDecipheriv(algorithm, new Buffer(Buffer.from(secret, 'base64')), ivSecret)
	let rawKey = decipherSecret.update(Buffer.from(cryptoObject['secret'], 'base64'))
	rawKey = Buffer.concat([rawKey, decipherSecret.final()])
	let decipherRaw = crypto.createDecipheriv(algorithm, rawKey, ivRaw)
	let data = decipherRaw.update(Buffer.from(cryptoObject['raw'], 'base64'))
	data = Buffer.concat([data, decipherRaw.final()])
	return JSON.parse(data.toString())
}

const sha256 = (message) => {
	return crypto.createHash('sha256').update(message).digest('hex');
}

export {
	sha256,
	encrypt,
	decrypt
}