<template>
  <v-container>
    <v-card
        class="mt-5"
        elevation="2"
        shaped
        v-if="value"
    >

      <v-card-title class="ml-2 text-title">{{ value['name'] }}</v-card-title>
      <v-card-text>
        <v-text-field
            ref="keyField"
            type="text"
            outlined
            :value="value['key']"
            append-icon="mdi-content-copy"
            @click:append="onCopyClick()"
        />
        <v-textarea
            label="Brief"
            outlined
            required
            disabled
            :value="value['extraInfo']"
        />
      </v-card-text>
    </v-card>
    <v-snackbar
        v-model="snackbarModel['show']"
        :timeout="snackbarModel['timeout']"
        class="text-center"
    >
      {{ this.snackbarModel['text'] }}
    </v-snackbar>
  </v-container>
</template>

<script>
export default {
  name: "Item",
  data() {
    return {
      snackbarModel: {
        show: false,
        text: 'Password copied!',
        timeout: 2000
      }
    }
  },
  props: {
    value: {
      required: true
    }
  },
  computed: {
    show: {
      get() {
        return this.value
      },
      set(value) {
        this.$emit('input', value)
      }
    },
  },
  methods: {
    onCopyClick: function () {
      this.$copyText(this.value['key'])
          .then(() => {
            this.snackbarModel['show'] = true
          }, function (e) {
            alert('Can not copy')
            console.log(e)
          })
    }
  }
}
</script>

<style scoped>
p {
  font-family: 'Raleway', sans-serif;
}

.text-title {
  font-family: 'Raleway', sans-serif;
  font-weight: 500;
}

.text {
  font-family: 'Raleway', sans-serif;
}
</style>