<template>
  <v-container>
    <v-row class="text-center">
      <v-col cols="12">
        <lottie :options="defaultOptions" :height="140" :width="170" v-on:animCreated="handleAnimation"/>
      </v-col>
      <v-col cols="12" class="mb-5">
        <h1 class="display-2">Secure Password</h1>
      </v-col>
      <v-col
          class="mb-5"
          cols="12"
      >
        <v-card
            class="mx-auto px-auto"
            elevation="3"
            :width="800"
            outlined
        >
          <v-tabs
              color="orange accent-4"
              right
          >
            <v-tab>Login</v-tab>
            <v-tab>Register</v-tab>
            <v-tab-item :key="1">
              <v-row>
                <v-spacer></v-spacer>
                <v-col cols="5">
                  <p class="text text-justify mt-3">
                    Please set your credentials:
                  </p>
                </v-col>
                <v-spacer></v-spacer>
                <v-col cols="5">
                  <p class="subheading text text-justify mt-3">
                    Choose cookie durability in order to remember user credentials:
                  </p>
                </v-col>
                <v-spacer></v-spacer>
              </v-row>
              <v-row justify="center">
                <v-spacer></v-spacer>
                <v-col cols="12" sm="10" md="5">
                  <v-form ref="loginForm">
                    <v-text-field
                        class="mb-3"
                        :rules="[rules.emailFormat]"
                        label="Email"
                        v-model="loginEmailField"
                        outlined
                    ></v-text-field>
                    <v-text-field
                        :append-icon="showPassword['login'] ? 'mdi-eye' : 'mdi-eye-off'"
                        :rules="[rules.required, rules.passwordMin]"
                        :type="showPassword['login'] ? 'text' : 'password'"
                        name="input-10-1"
                        label="Password"
                        v-model="loginPasswordField"
                        @click:append="showPassword['login'] = !showPassword['login']"
                        outlined
                    ></v-text-field>
                  </v-form>
                </v-col>
                <v-spacer></v-spacer>
                <v-col cols="12" sm="10" md="5">
                  <v-radio-group
                      class="text my-auto"
                      v-model="cookiesField"
                  >
                    <v-radio
                        label="No cookies please! (Recommended)"
                        value="cookie-type-0"
                        color="secondary"
                    ></v-radio>
                    <v-radio
                        label="1 hour"
                        value="cookie-type-1"
                        color="secondary"
                    ></v-radio>
                    <v-radio
                        label="6 hours"
                        value="cookie-type-2"
                        color="secondary"
                    ></v-radio>
                    <v-radio
                        label="24 hours (Not recommended)"
                        value="cookie-type-3"
                        color="secondary"
                    ></v-radio>
                  </v-radio-group>
                </v-col>
                <v-spacer></v-spacer>
              </v-row>
              <v-row>
                <v-col>
                  <v-btn
                      :loading="loadingLogin"
                      :disabled="loadingLogin"
                      color="secondary"
                      @click="loaderLogin = 'loadingLogin'"
                      v-on:click="submitLogin"
                  >
                    Submit
                  </v-btn>
                </v-col>
              </v-row>
            </v-tab-item>
            <v-tab-item :key="2">
              <v-row align="center" justify="center">
                <v-col cols="10">
                  <p class="text text-justify mt-3">
                    Please set an email and password, once done you will be able to start storing you secret passwords safely.
                    <br>
                    Make sure your password set is a <strong>strength once and keep it mind</strong> as it will be a master password for
                    all stored passwords, so it worth be carefully :).
                  </p>
                </v-col>
              </v-row>
              <v-row align="center" justify="center">
                <v-spacer></v-spacer>
                <v-col cols="5">
                  <v-form ref="registerForm">
                    <v-text-field
                        class="mb-3"
                        :rules="[rules.emailFormat]"
                        label="Email"
                        v-model="registerEmailField"
                        outlined
                    ></v-text-field>
                    <v-text-field
                        class="mb-3"
                        :append-icon="showPassword['register'] ? 'mdi-eye' : 'mdi-eye-off'"
                        :rules="[rules.required, rules.passwordMin]"
                        :type="showPassword['register'] ? 'text' : 'password'"
                        name="input-10-1"
                        label="Password"
                        v-model="registerPasswordField"
                        @click:append="showPassword['register'] = !showPassword['register']"
                        outlined
                    ></v-text-field>
                    <v-text-field
                        :append-icon="showPassword['registerRepeat'] ? 'mdi-eye' : 'mdi-eye-off'"
                        :rules="[rules.required, rules.passwordMin, rules.passwordRepeat]"
                        :type="showPassword['registerRepeat'] ? 'text' : 'password'"
                        name="input-10-1"
                        label="Repeat Password"
                        v-model="registerPassword2Field"
                        @click:append="showPassword['registerRepeat'] = !showPassword['registerRepeat']"
                        outlined
                    ></v-text-field>
                    <v-btn
                        class="ma-2"
                        :loading="loadingRegister"
                        :disabled="loadingRegister"
                        color="secondary"
                        @click="loaderRegister = 'loadingRegister'"
                        v-on:click="submitRegister"
                    >
                      Submit
                    </v-btn>
                  </v-form>
                </v-col>
                <v-spacer></v-spacer>
              </v-row>
            </v-tab-item>
          </v-tabs>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Lottie from 'vue-lottie';
import * as animationData from '../assets/splash_anim.json';

export default {
  name: "Login",
  components: {
    'lottie': Lottie
  },
  data() {
    return {
      defaultOptions: {animationData: animationData.default},
      animationSpeed: 1,
      showPassword: {
        login: false,
        register: false,
        registerRepeat: false
      },
      passwordMinimumLength: 8,
      loaderLogin: null,
      loadingLogin: false,
      loaderRegister: null,
      loadingRegister: false,
      loginEmailField: null,
      loginPasswordField: null,
      registerEmailField: null,
      registerPasswordField: null,
      registerPassword2Field: null,
      cookiesField: 'cookie-type-0',
      rules: {
        required: (value) => (value || '').length > 0 || 'Required field!',
        passwordMin: (value) => (value || '').length >= this.passwordMinimumLength ||
            `Minimum password length is ${this.passwordMinimumLength} characters`,
        emailFormat: (value) => /^(.*@.*\.com|.*@.*\.es)$/.test(value || '') || `Invalid email format.`,
        passwordRepeat: (value) => value === this.registerPasswordField || `Password doesn't match.`
      }
    }
  },

  watch: {
    loaderLogin() {
      const l = this.loaderLogin
      this[l] = !this[l]
      setTimeout(() => (this[l] = false), 30000)
      this.loaderLogin = null
    },
    loaderRegister() {
      const l = this.loaderRegister
      this[l] = !this[l]
      setTimeout(() => (this[l] = false), 30000)
      this.loaderRegister = null
    }
  },

  methods: {
    handleAnimation(anim) {
      this.anim = anim;
    },
    submitRegister() {

      if (this.$refs.registerForm.validate()) {
        this.$store.dispatch('login/signup', {
          email : this.registerEmailField,
          password : this.registerPasswordField,
          cookie: this.cookiesField
        }).catch((error) => {
          this[this.loadingRegister] = false
          alert(error)
        })
      } else {
        alert("Form register denied :(")
        this[this.loadingRegister] = false
      }
    },
    submitLogin() {
      if (this.$refs.loginForm.validate()) {
        this.$store.dispatch('login/login', {
          email : this.loginEmailField,
          password : this.loginPasswordField,
          cookieValue: this.cookiesField
        }).catch((error) => {
          this[this.loadingLogin] = false
          alert(error)
        })
      } else {
        alert("Form login denied :(")
        this[this.loadingLogin] = false
      }
    }
  }
}
</script>

<style scoped>
h1 {
  font-family: 'Poppins', sans-serif;
}

.text {
  font-family: 'Raleway', sans-serif;
}
</style>