<template>
  <v-dialog
      v-model="model['show']"
      persistent
      max-width="400px"
  >
    <v-card>
      <v-card-title>
        <p class="overline">Choose directory to move</p>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-row>
            <v-col cols="12">
              <v-list
                  rounded
              >
                <v-list-item-group
                    v-model="selectedDir"
                    color="secondary"
                >
                  <v-list-item
                      v-for="directory in getDisplayingDirs()"
                      :key="directory['id']"
                      @dblclick="selectItem(directory)"
                  >
                    <v-list-item-avatar>
                      <v-icon
                          class="grey darken-3"
                          dark
                      >
                        mdi-folder
                      </v-icon>
                    </v-list-item-avatar>
                    <v-list-item-content>
                      <v-list-item-title class="text-bold" v-text="directory['name']"></v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>

                </v-list-item-group>
              </v-list>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-btn
            color="orange darken-3"
            text
            @click="onBackClick()"
            :disabled="displayingDir.id === 'ROOT'"
        >
          Back
        </v-btn>
        <v-spacer></v-spacer>
        <v-btn
            color="orange darken-3"
            text
            @click="onCancelClick()"
        >
          Close
        </v-btn>
        <v-btn
            color="orange darken-3"
            text
            @click="onMoveClick()"
        >
          Move
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "EntryMoveModal",
  data() {
    return {
      numOfColumns: 4,
      selectedDir: null,
      displayingDir: {
        id: 'ROOT',
      }
    }
  },
  props: {
    value: {
      required: true
    }
  },
  computed: {
    model: {
      get() {
        return this.value
      },
      set(value) {
        this.$emit('input', value)
      }
    }
  },
  methods: {
    selectItem: function (dir) {
      this.displayingDir = {
        id: dir.id,
        parentId: dir.parentId
      }
    },
    getDisplayingDirs: function () {
      return this.model['dirs'].filter(i => i['parentId'] === this.displayingDir['id'] && i['id'] !== this.model['selectedItem']['id'])
    },
    onBackClick: function () {
      if (this.displayingDir['id'] !== 'ROOT') {
        if (this.displayingDir['parentId'] === 'ROOT') {
          this.displayingDir = {id: 'ROOT'}
        } else {
          this.displayingDir = this.model['dirs'].find(i => i['id'] === this.displayingDir['parentId'])
        }
      }
    },
    onMoveClick: function () {
      const dirs = this.getDisplayingDirs()
      const item = this.model['selectedItem']
      let moveTo
      if (this.selectedDir == null && this.displayingDir['id'] === 'ROOT') {
        moveTo = 'ROOT'
      } else {
        moveTo = this.selectedDir < dirs.length ? dirs[this.selectedDir]['id'] : null
      }
      if (moveTo) {
        let updateStoreService = "dashboard/".concat(item['isDirectory'] ? "updateStoredDirParent" : "updateStoredEntryParent")
        this.$store.dispatch(updateStoreService, {
          id: item['id'],
          newParentId: moveTo
        })
        let service = "dashboard/".concat(item['isDirectory'] ? "updateDirectory" : "updateEntry")
        this.$store.dispatch(service, {
          data: {
            id: item['id'],
          },
          callback: () => {
            this.model['show'] = false
            this.model['onSuccess']()
          },
          error: (error) => {
            this.model['onError'](error)
          }
        })
      }
    },
    onCancelClick: function () {
      this.model['show'] = false
    }
  }
}
</script>

<style scoped>
.overline {
  font-family: 'Raleway', sans-serif !important;
  font-size: 18px !important;
}

.text {
  font-family: 'Raleway', sans-serif;
}
</style>