<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12" sm="3" md="6">
        <v-breadcrumbs :items="breadcrumbs">
          <template v-slot:item="{ item }">
            <v-breadcrumbs-item
                :class="item['disabled'] ? '' : 'link'"
                @click="onBreadcrumbClick(item['id'])"
                :disabled="item['disabled']"
            >
              {{ item['text'] }}
            </v-breadcrumbs-item>
          </template>
          <template v-slot:divider>
            <v-icon>mdi-chevron-right</v-icon>
          </template>
        </v-breadcrumbs>
        <v-list
            class="mt-3"
            subheader
            two-line
            rounded
        >
          <v-list-item-group
              v-model="selectedItem"
              color="secondary"
          >
            <v-divider inset></v-divider>
            <v-subheader inset class="text">Folders</v-subheader>
            <v-list-item
                v-for="directory in displayingDirectories"
                :key="directory['id']"
                @contextmenu="showContextMenuAction($event, directory)"
                @dblclick="selectItem(directory)"
            >
              <v-list-item-avatar>
                <v-icon
                    class="grey darken-3"
                    dark
                >
                  mdi-folder
                </v-icon>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title class="text-bold" v-text="directory['name']"></v-list-item-title>
              </v-list-item-content>
              <v-list-item-action>
                <v-tooltip
                    v-model="showTooltip.find(i => i['id'] === directory['id'])['value']"
                    right
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                        icon
                        v-bind="attrs"
                        @mouseover="onInfoClick(directory)"
                        @mouseleave="showTooltip.find(i => i['id'] === directory['id'])['value'] = false"
                    >
                      <v-icon color="grey lighten-1">mdi-information</v-icon>
                    </v-btn>
                  </template>
                  <div
                      :class="index === 0 ? 'text mt-1' : 'text my-1'"
                      v-for="(tooltip, index) in tooltipItems"
                      :key="index"
                  >
                    <span>{{ tooltip }}</span>
                  </div>
                </v-tooltip>
              </v-list-item-action>
            </v-list-item>
            <EmptyState v-model="emptyStateDirs"/>

            <v-divider inset></v-divider>
            <v-subheader inset class="text">Passwords</v-subheader>
            <v-list-item
                v-for="entry in displayingEntries"
                :key="entry['id']"
                @contextmenu="showContextMenuAction($event, entry)"
            >
              <v-list-item-avatar>
                <v-icon
                    class="orange darken-4"
                    dark
                >
                  mdi-key
                </v-icon>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title class="text-bold" v-text="entry['name']"></v-list-item-title>
              </v-list-item-content>
              <v-list-item-action>
                <v-tooltip
                    v-model="showTooltip.find(i => i['id'] === entry['id'])['value']"
                    right
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                        icon
                        v-bind="attrs"
                        @mouseover="onInfoClick(entry)"
                        @mouseleave="showTooltip.find(i => i['id'] === entry['id'])['value'] = false"
                    >
                      <v-icon color="grey lighten-1">mdi-information</v-icon>
                    </v-btn>
                  </template>
                  <div
                      :class="index === 0 ? 'text mt-1' : 'text my-1'"
                      v-for="(tooltip, index) in tooltipItems"
                      :key="index"
                  >
                    <span>{{ tooltip }}</span>
                  </div>
                </v-tooltip>
              </v-list-item-action>
            </v-list-item>
            <EmptyState v-model="emptyStateEntries"/>

          </v-list-item-group>
        </v-list>
      </v-col>
      <v-col cols="12" sm="3" md="6">
        <ItemDetails v-model="selectedItemData"/>
      </v-col>
    </v-row>
    <v-speed-dial
        v-model="fab"
        :top="top"
        :bottom="bottom"
        :right="right"
        :left="left"
        :direction="direction"
        :open-on-hover="hover"
        :transition="transition"
        style="position: absolute"
    >
      <template v-slot:activator>
        <v-btn
            v-model="fab"
            color="orange darken-3"
            dark
            fab
        >
          <v-icon v-if="fab">
            mdi-close
          </v-icon>
          <v-icon v-else>
            mdi-plus
          </v-icon>
        </v-btn>
      </template>

      <v-btn
          fab
          dark
          small
          color="orange lighten-1"
          @click.stop="dialogDirectory['show'] = true"
      >
        <v-icon>mdi-folder</v-icon>
      </v-btn>
      <v-btn
          fab
          dark
          small
          color="orange lighten-1"
          @click.stop="dialogKey['show'] = true"
      >
        <v-icon>mdi-key</v-icon>
      </v-btn>
    </v-speed-dial>
    <EntryDirectoryModal v-model="dialogDirectory"/>
    <EntryKeyModal v-model="dialogKey"/>
    <EntryMoveModal v-model="dialogMove"/>
    <ConfirmationModal v-model="dialogConfirmation"/>
    <RenameModal v-model="dialogRename"/>
    <v-menu
        v-model="showContextMenu"
        :position-x="contextMenuX"
        :position-y="contextMenuY"
        absolute
        rounded="b-lg"
        offset-y
    >
      <v-list>
        <v-list-item
            v-for="(item, index) in contextMenuItems"
            :key="index"
            @click="contextMenuItemClick(item)"
            link
        >
          <v-list-item-avatar
              size="25"
          >
            <v-icon
                size="15"
                class="grey darken-3"
                dark
            >
              {{ item.icon }}
            </v-icon>
          </v-list-item-avatar>
          <v-list-item-title class="mx-1">{{ item.title }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </v-container>
</template>

<script>
import EntryKeyModal from "@/components/EntryKeyModal";
import EntryDirectoryModal from "@/components/EntryDirectoryModal";
import ItemDetails from "@/components/ItemDetails";
import EntryMoveModal from "@/components/EntryMoveModal";
import ConfirmationModal from "@/components/ConfirmationModal";
import RenameModal from "@/components/RenameModal";
import EmptyState from "@/components/EmptyState";

export default {
  name: "Dashboard",
  data() {
    return {
      direction: 'left',
      fab: false,
      hover: true,
      tabs: null,
      top: false,
      right: true,
      bottom: true,
      left: false,
      transition: 'slide-x-reverse-transition',
      dialogKey: {
        show: false,
        currentDirId: 'ROOT',
        onSuccess: () => {
          this.fetchData()
        },
        onError: (error) => {
          console.log('Error from view: ' + error)
        }
      },
      dialogDirectory: {
        show: false,
        currentDirId: 'ROOT',
        onSuccess: () => {
          this.fetchData()
        },
        onError: (error) => {
          console.log('Error from view: ' + error)
        }
      },
      dialogMove: {
        show: false,
        dirs: [],
        selectedItem: null,
        onSuccess: () => this.fetchData(),
        onError: (error) => console.log('Error from view: ' + error),
        onCancel: () => this.dialogMove['selectedItem'] = null
      },
      dialogConfirmation: {
        show: false,
        selectedItem: null,
        onConfirm: () => this.fetchData(),
        onCancel: () => this.dialogConfirmation['selectedItem'] = null
      },
      dialogRename: {
        show: false,
        selectedItem: null,
        name: '',
        onError: (error) => console.log('Error from view: ' + error),
        onCancel: () => this.dialogRename['selectedItem'] = null
      },
      directories: [],
      entries: [],
      displayingDirectories: [],
      displayingEntries: [],
      showContextMenu: false,
      showTooltip: [],
      tooltipItems: [],
      contextMenuX: 0,
      contextMenuY: 0,
      contextMenuData: null,
      contextMenuItems: [],
      selectedItem: null,
      selectedItemData: null,
      currentDirectory: {
        id: 'ROOT',
        name: 'Home'
      },
      breadcrumbs: [{
        text: 'Home',
        id: 'ROOT',
        disabled: true
      }],
      emptyStateDirs: {
        show: false,
        dirOrEntry: "directories"
      },
      emptyStateEntries: {
        show: false,
        dirOrEntry: "entries"
      }
    }
  },

  created() {
    this.fetchData()
    this.setCloseListener()
  },

  watch: {
    currentDirectory: function (dir) {
      this.displayingDirectories = this.directories.filter(d => d['parentId'] === dir['id'])
      this.displayingEntries = this.entries.filter(e => e['parentId'] === dir['id'])
      this.breadcrumbs = this.getBreadcrumbItems()
      this.dialogKey['currentDirId'] = dir['id']
      this.dialogDirectory['currentDirId'] = dir['id']
    },
    selectedItem: function (value) {
      if (value >= this.displayingDirectories.length) {
        this.selectedItemData = this.displayingEntries[value - this.displayingDirectories.length]
      } else {
        this.selectedItemData = null
      }

    },
    displayingDirectories: function (value) {
      value.forEach(i => {
        this.showTooltip.push({id: i['id'], value: false})
      })
      this.emptyStateDirs['show'] = value.length === 0
    },
    displayingEntries: function (value) {
      value.forEach(i => {
        this.showTooltip.push({id: i['id'], value: false})
      })
      this.emptyStateEntries['show'] = value.length === 0
    }
  },

  methods: {
    fetchData() {
      this.$store.dispatch('dashboard/fetchData', {
        callback: () => {
          this.$store.dispatch('dashboard/fetchDirectories', {
            callback: (data) => {
              this.directories = data
              this.displayingDirectories = data.filter(d => d['parentId'] === this.currentDirectory['id'])
            },
            error: (error) => {
              console.log('error from view: ' + error)
            }
          })
          this.$store.dispatch('dashboard/fetchEntries', {
            callback: (data) => {
              this.entries = data
              this.displayingEntries = data.filter(e => e['parentId'] === this.currentDirectory['id'])
            },
            error: (error) => {
              console.log('error from view: ' + error)
            }
          })
        },
        error: (error) => {
          console.log('error from view: ' + error)
        }
      })
    },
    showContextMenuAction(e, entry) {
      e.preventDefault()
      this.showContextMenu = false
      this.contextMenuX = e.clientX
      this.contextMenuY = e.clientY
      this.$nextTick(() => {
        this.showContextMenu = true
      })
      this.contextMenuItems = this.obtainContextMenuItems(entry)
      this.contextMenuData = entry
    },
    obtainContextMenuItems(entry) {
      const items = []
      items.push({
        id: 1,
        title: 'Move',
        item: entry,
        icon: 'mdi-folder-move'
      })
      if (entry['isDirectory']) {
        items.push({
          id: 2,
          title: 'Rename',
          item: entry,
          icon: 'mdi-circle-edit-outline'
        })
      }
      items.push({
        id: 3,
        title: 'Delete',
        item: entry,
        icon: 'mdi-delete-outline'
      })
      return items
    },
    contextMenuItemClick(contextMenuItem) {
      switch (contextMenuItem['id']) {
        case 1: // move
          this.dialogMove['selectedItem'] = contextMenuItem['item']
          this.dialogMove['dirs'] = this.directories
          this.dialogMove['show'] = true
          break;
        case 2: // rename
          this.dialogRename['selectedItem'] = contextMenuItem['item']
          this.dialogRename['name'] = contextMenuItem['item']['name']
          this.dialogRename['show'] = true
          break;
        case 3: // delete
          this.dialogConfirmation['selectedItem'] = contextMenuItem['item']
          this.dialogConfirmation['show'] = true
          break
      }
    },
    onInfoClick(data) {
      if (!this.showTooltip.find(i => i['id'] === data['id'])['value']) {
        if (!data['isDirectory']) {
          const creation = 'Created on: ' + this.formatDate(new Date(data['dateCreation']))
          let update
          if (data['dateUpdate']) {
            update = 'Updated on: ' + this.formatDate(new Date(data['dateUpdate']))
          } else {
            update = null
          }
          this.tooltipItems = [creation, update]
        } else {
          let contentDirs = 0
          let contentKeys = 0
          this.directories.forEach((dir) => {
            if (dir['parentId'] === data['id']) contentDirs++
          })
          this.entries.forEach((entry) => {
            if (entry['parentId'] === data['id']) contentKeys++
          })
          this.tooltipItems = [`${contentDirs} directories`, `${contentKeys} passwords`]
        }
        if (this.tooltipItems.length > 0) {
          this.showTooltip.find(i => i['id'] === data['id'])['value'] = true
        }
      }
    },
    formatDate(date) {
      let month = '' + (date.getMonth() + 1)
      let day = '' + date.getDate()
      const year = date.getFullYear()
      if (month.length < 2)
        month = '0' + month;
      if (day.length < 2)
        day = '0' + day;

      return [day, month, year].join('-');
    },
    getBreadcrumbItems() {
      const breadcrumbs = []
      const items = this.obtainBreadcrumbOf(this.currentDirectory)
      if (this.currentDirectory['id'] !== 'ROOT') {
        items.reverse()
        items.push(this.currentDirectory)
      }
      items.forEach((i) => {
        breadcrumbs.push({text: i['name'], id: i['id'], disabled: i['id'] === this.currentDirectory['id']})
      })
      return breadcrumbs
    },
    obtainBreadcrumbOf(dir) {
      let result = []
      if (dir['id'] === 'ROOT' || dir['parentId'] === 'ROOT') {
        return [{id: 'ROOT', name: 'Home'}]
      } else {
        const d = this.directories.find(d => d['id'] === dir['parentId'])
        result.push(d)
        result = result.concat(this.obtainBreadcrumbOf(d))
        return result
      }
    },
    onBreadcrumbClick(id) {
      if (id === 'ROOT') this.currentDirectory = {id: 'ROOT', name: 'Home'}
      else this.currentDirectory = this.directories.find(d => d['id'] === id)
    },
    selectItem(dir) {
      this.currentDirectory = dir
    },
    setCloseListener() {
      window.addEventListener('beforeunload', () => {
        if (window.performance.navigation.type !== PerformanceNavigation.TYPE_RELOAD) {
          this.$store.dispatch('dashboard/logout')
        }
      })
    }
  },

  components: {
    EmptyState,
    EntryKeyModal,
    EntryDirectoryModal,
    ItemDetails,
    ConfirmationModal,
    RenameModal,
    EntryMoveModal,
  }
}
</script>

<style scoped>

.link {
  text-decoration: underline;
}

.link:hover {
  cursor: pointer;
}

h1 {
  font-family: 'Poppins', sans-serif;
}

.text-bold {
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
}

.text {
  font-family: 'Raleway', sans-serif;
}
</style>