<template>
  <v-dialog
      v-model="model['show']"
      persistent
      max-width="600px"
  >
    <v-card>
      <v-card-title>
        <p class="overline">Generate options</p>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-row>
            <v-col cols="12">
              <span>Complexity:</span>
            </v-col>
            <div class="mt-2">
              <v-chip
                  class="clickable ml-3"
                  :color="complexitySelected === 0 ? 'orange lighten-1' : 'gray'"
                  filter
                  @click="complexitySelected = 0"
              >
                Simple
              </v-chip>
              <v-chip
                  class="clickable ml-3"
                  :color="complexitySelected === 1 ? 'orange lighten-1' : 'gray'"
                  filter
                  @click="complexitySelected = 1"
              >
                Complex
              </v-chip>
            </div>
            <v-col cols="12" class="mt-4">
              <span>Length:</span>
            </v-col>
            <v-col cols="4">
              <v-text-field
                  class="centered-input"
                  v-model="length"
                  label="Number"
                  append-icon="mdi-chevron-right"
                  @click:append="increment"
                  prepend-inner-icon="mdi-chevron-left"
                  @click:prepend-inner="decrement"
                  hide-details
                  single-line
                  outlined>
              </v-text-field>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
            color="orange darken-3"
            text
            @click="model['show'] = false"
        >
          Close
        </v-btn>
        <v-btn
            color="orange darken-3"
            text
            @click="onGenerate()"
        >
          Generate
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "EntryKeyGenerateModal",
  props: {
    value: {
      required: true
    }
  },
  data() {
    return {
      complexitySelected: 0,
      length: 12,
      predefinedLength: [4, 6, 8, 12, 16, 24, 32, 64, 128]
    }
  },
  computed: {
    model: {
      get() {
        return this.value
      },
      set(value) {
        this.$emit('input', value)
      }
    }
  },

  methods: {
    increment() {
      let num
      if (typeof this.length === 'string') {
        num = parseInt(this.length)
      } else {
        num = this.length
      }
      if (!isNaN(num)) {
        const index = this.predefinedLength.indexOf(num)
        if (index !== -1) {
          const maxIndex = this.predefinedLength.length - 1
          this.length = this.predefinedLength[index !== maxIndex ? index + 1 : maxIndex]
        } else {
          for (let i = 0; i < this.predefinedLength.length - 1 ; i++) {
            if (num < this.predefinedLength[i]) {
              this.length = this.predefinedLength[i]
              break
            }
          }
        }
      }
    },
    decrement() {
      let num
      if (typeof this.length === 'string') {
        num = parseInt(this.length)
      } else {
        num = this.length
      }
      if (!isNaN(num)) {
        const index = this.predefinedLength.indexOf(num)
        if (index !== -1) {
          this.length = this.predefinedLength[index !== 0 ? index - 1 : 0]
        } else {
          const reversed = this.predefinedLength.slice()
          reversed.reverse()
          for (let i = 0; i < reversed.length ; i++) {
            if (num > reversed[i]) {
              this.length = reversed[i]
              break
            }
          }
        }
      }
    },
    onGenerate() {
      this.$store.dispatch('dashboard/generateKey', {
        isSimple: this.complexitySelected === 0,
        length: this.length,
        onSuccess: (value) => {
          this.model['callback'](value)
          this.model['show'] = false
        },
        onError: error => console.log('Error from view: ' + error)
      })
    }
  }
}
</script>

<style scoped>

.centered-input >>> input {
  text-align: center
}

.clickable {

}

.clickable:hover {
  cursor: pointer;
}
</style>