import * as firebase from "@/firebase/firebase";
import {encrypt, decrypt} from "@/util/CryptoUtils"

const DIR_PATH = "/dir"
const ENTRY_PATH = "/entry"

const fetch = async (path, error) => {
	return await firebase.db
		.ref(path)
		.once('value')
		.catch(error)
}

const fetchData = async (error) => {
	const dirsSnap = await fetch(firebase.auth.currentUser.uid + DIR_PATH, error)
	const entriesSnap = await fetch(firebase.auth.currentUser.uid + ENTRY_PATH, error)
	const entries = []
	const dirs = []
	let data
	let decrypted

	entriesSnap.forEach((child) => {
		data = JSON.parse(child.val())
		decrypted = decrypt(data)
		entries.push(decrypted)
	})

	dirsSnap.forEach((child) => {
		data = JSON.parse(child.val())
		decrypted = decrypt(data)
		dirs.push(decrypted)
	})

	return {
		entries: entries,
		dirs: dirs
	}
}

const insertDirectory = async (data, onSuccess, error) => {
	const path = firebase.auth.currentUser.uid + DIR_PATH + `/${data['id']}`
	const d = JSON.stringify(encrypt(data))
	await firebase.db.ref(path).set(d, (e) => {
		if (e) error(e); else onSuccess()
	})
}

const updateDirectory = async (data, onSuccess, error) => {
	await deleteEntry(
		data,
		true,
		async function () {
			await insertDirectory(data, onSuccess, error)
		},
		error
	)
}

const insertEntry = async (data, onSuccess, error) => {
	const path = firebase.auth.currentUser.uid + ENTRY_PATH + `/${data['id']}`
	const d = JSON.stringify(encrypt(data))
	await firebase.db.ref(path).set(d, (e) => {
		if (e) error(e); else onSuccess()
	})
}

const updateEntry = async (data, onSuccess, error) => {
	await deleteEntry(
		data,
		false,
		async () => {
			await insertEntry(data, onSuccess, error)
		},
		error
	)
}

const deleteEntry = async (data, isDirectory, onSuccess, error) => {
	const path = firebase.auth.currentUser.uid + (isDirectory ? DIR_PATH : ENTRY_PATH ) + `/${data['id']}`
	await firebase.db.ref(path).remove(async (e) => {
		if (e) error(e); else await onSuccess()
	})
}

const logout = () => {
	firebase.auth.signOut()
		.catch((e) =>{
			console.log(e)
		})
}

export {
	fetchData,
	insertDirectory,
	updateDirectory,
	insertEntry,
	updateEntry,
	deleteEntry,
	logout
}