import * as crypto from "@/util/CryptoUtils";
import * as firebase from "@/firebase/firebase";
import * as cookie from "@/util/CookiesUtils";

const setPersistence = (next, error) => {
	firebase.auth.setPersistence(firebase.persistence.SESSION)
		.then(next)
		.catch(error)
}

const signup = async (data, callback, error) => {
	const pwd = crypto.sha256(data.password)
	setPersistence(
		() => {
			firebase.auth.createUserWithEmailAndPassword(data.email, pwd)
				.then(() => {
					cookie.setCookie({
						email: data.email,
						pwd: pwd,
						time: -1
					})
					callback()
				})
				.catch(error)
		},
		error
	)
}

const login = async (data, callback, error) => {
	const pwd = crypto.sha256(data.password)
	setPersistence(
		() => {
			firebase.auth.signInWithEmailAndPassword(data.email, pwd)
				.then(() => {
					cookie.setCookie({
						email: data.email,
						pwd: pwd,
						time: -1
					})
					callback()
				})
				.catch(error)
		},
		error
	)
}

export {
	signup,
	login
}