import Vue from 'vue'
import VueRouter from 'vue-router'
import Dashboard from '../components/Dashboard.vue'
import Login from '../components/Login.vue'
import * as firebase from '@/firebase/firebase'
import * as cookies from '@/util/CookiesUtils'

Vue.use(VueRouter)

const routes = [
	{
		path: '/',
		name: 'Dashboard',
		component: Dashboard,
		meta: {
			requiresAuth: true
		}
	},
	{
		path: '/login',
		name: 'Login',
		component: Login
	},
]

const router = new VueRouter({
	mode: 'history',
	base: process.env.BASE_URL,
	routes
})
router.beforeEach(async (to, from, next) => {
	const requiresAuth = to.matched.some(x => x.meta.requiresAuth)
	if (!firebase.auth.currentUser && cookies.isAutoLogin()) {
		const data = cookies.getCookieData()
		firebase.auth.signInWithEmailAndPassword(data.email, data.pwd)
			.then(() => {
				next()
			})
			.catch(() => {
				next('/login')
			})
	} else if (requiresAuth && !firebase.auth.currentUser) {
		next('/login')
	} else {
		next()
	}
})

export default router