<template>
  <v-dialog
      v-model="model['show']"
      persistent
      max-width="400px"
  >
    <v-card>
      <v-card-title class="red darken-3">
        <p class="overline">Confirmation</p>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-row>
            <v-col cols="12">
              <p class="text">
                Are you sure you want to delete this entry?
                This action will not be undone
              </p>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
            color="orange darken-3"
            text
            @click="onCancelClick()"
        >
          Cancel
        </v-btn>
        <v-btn
            color="orange darken-3"
            text
            @click="onConfirmClick()"
        >
          Confirm
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: "ConfirmationModal",
  data() {
    return {
    }
  },
  props: {
    value: {
      required: true
    }
  },
  computed: {
    model: {
      get () {
        return this.value
      },
      set (value) {
        this.$emit('input', value)
      }
    }
  },
  methods: {
    onConfirmClick: function () {
      this.$store.dispatch('dashboard/deleteEntry', {
        data: {
          item: this.model['selectedItem']
        },
        callback: () => {
          this.model['show'] = false
          this.model['onConfirm']()
        },
        error: (error) => {
          this.model['onError'](error)
        }
      })
    },
    onCancelClick: function() {
      this.model['onCancel']()
      this.model['show'] = false
    }
  }
}
</script>

<style scoped>
.overline {
  font-family: 'Raleway', sans-serif !important;
  font-size: 18px !important;
}
.text {
  font-family: 'Raleway', sans-serif;
}
</style>