import * as loginService from "@/firebase/api/loginService"
import router from "@/router/router";

const actions = {
	async signup(d, form) {
		await loginService.signup(
			form,
			async () => await router.push('/'),
			error => router.onError(error)
		)
	},

	async login(d, form) {
		await loginService.login(
			form,
			async () => await router.push('/'),
			error => router.onError(error)
		)
	}
}

export default {
	namespaced: true,
	actions
}