<template>
  <v-dialog
      v-model="model['show']"
      persistent
      max-width="600px"
  >
    <v-card>
      <v-card-title>
        <p class="overline">New password</p>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-row>
            <v-col cols="12">
              <v-text-field
                  label="Account name"
                  type="email"
                  ref="accountField"
                  v-model="accountModel"
                  :rules="[rules.required]"
                  required
                  outlined
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="9" sm="12">
              <v-text-field
                  label="Password"
                  type="text"
                  ref="passwordField"
                  v-model="pwdModel"
                  :rules="[rules.required]"
                  required
                  outlined
              ></v-text-field>
            </v-col>
            <v-col cols="12" md="3" sm="12">
              <v-btn
                  class="align-middle"
                  color="orange darken-1"
                  v-model="briefModel"
                  outlined
                  @click.stop="showGenerateKeyDialog()"
              >
                generate
              </v-btn>
            </v-col>
            <v-col cols="12">
              <v-textarea
                  label="Brief"
                  outlined
                  required
              ></v-textarea>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
            color="orange darken-3"
            text
            @click="model['show'] = false"
        >
          Close
        </v-btn>
        <v-btn
            color="orange darken-3"
            text
            @click="validate()"
        >
          Save
        </v-btn>
      </v-card-actions>
    </v-card>
    <EntryKeyGenerateModal v-model="dialogGeneration" />
  </v-dialog>
</template>

<script>
import EntryKeyGenerateModal from "@/components/EntryKeyGenerateModal";
import UUID from '@/util/UUID'

export default {
  name: "EntryKeyModal",
  props: {
    value: {
      required: true
    }
  },
  data() {
    return {
      accountModel: null,
      pwdModel: null,
      briefModel: null,
      dialogGeneration: {
        show: false,
        callback: this.onKeyGenerated
      },
      rules: {
        required: (value) => (value || '').length > 0 || 'Required field!'
      }
    }
  },
  computed: {
    model: {
      get () {
        return this.value
      },
      set (value) {
        this.$emit('input', value)
      }
    }
  },
  methods: {
    showGenerateKeyDialog() {
      this.dialogGeneration['show'] = true
    },
    onKeyGenerated(value) {
      this.pwdModel = value
    },
    validate() {
      const isAccountValid = this.$refs.accountField.validate(true)
      const isPwdValid = this.$refs.passwordField.validate(true)

      if (isAccountValid && isPwdValid) {

        this.$store.dispatch('dashboard/insertEntry', {
          data: {
            id: UUID.generate(),
            isDirectory: false,
            name: this.accountModel,
            dateCreation: new Date().getTime(),
            dateUpdate: new Date().getTime(),
            extraInfo: this.briefModel,
            key: this.pwdModel,
            keyComplexity: 'UNKNOWN',
            keyLength: 'LENGTH_CUSTOM',
            parentId: this.model['currentDirId']
          },
          callback: () => {
            this.model['show'] = false
            this.model['onSuccess']()
          },
          error: (error) => {
            this.model['onError'](error)
          }
        })

      } else {
        this.model['show'] = true
      }
    }
  },
  components: {
    EntryKeyGenerateModal
  }
}
</script>

<style scoped>
.overline {
  font-family: 'Raleway', sans-serif !important;
  font-size: 18px !important;
}
</style>