import * as dashboardService from "@/firebase/api/dashboardService";
import {generate} from "@/util/GenerateUtils"

const state = () => ({
	directories: [],
	entries: []
})

const getters = {
	getDirectories: (state) => {
		return state.directories
	},
	getEntries: (state) => {
		return state.entries
	},
	getAllData: (state, getters) => {
		return getters.getDirectories.concat(getters.getEntries)
	}
}

const mutations = {
	setDirectories: (state, dirs) => {
		state.directories = dirs
	},
	setEntries: (state, entries) => {
		state.entries = entries
	},
	updateStoredDirName: (state, payload) => {
		state.directories.find(i => i['id'] === payload['id'])['name'] = payload['name']
	},
	updateStoredDirParent: (state, payload) => {
		state.directories.find(i => i['id'] === payload['id'])['parentId'] = payload['newParentId']
	},
	updateStoredEntryParent: (state, payload) => {
		state.entries.find(i => i['id'] === payload['id'])['parentId'] = payload['newParentId']
	}
}


const actions = {
	async fetchData({commit, getters}, payload) {
		const response = await dashboardService.fetchData(payload['error'])
		commit('setDirectories', response.dirs)
		commit('setEntries', response.entries)
		payload['callback'](getters.getAllData)
	},
	async fetchDirectories({commit, getters}, payload) {
		if (getters.getDirectories && getters.getDirectories.length > 0) {
			payload['callback'](getters.getDirectories)
		} else {
			const response = await dashboardService.fetchData(payload['error'])
			commit('setDirectories', response.dirs)
			commit('setEntries', response.entries)
			payload['callback'](getters.getAllData.filter(i => i['isDirectory']))
		}
	},
	async fetchEntries({commit, getters}, payload) {
		if (getters.getEntries && getters.getEntries.length > 0) {
			payload['callback'](getters.getEntries)
		} else {
			const response = await dashboardService.fetchData(payload['error'])
			commit('setDirectories', response.dirs)
			commit('setEntries', response.entries)
			payload['callback'](getters.getAllData.filter(i => i['getEntries']))
		}
	},
	async updateDirectory({getters}, payload) {
		const dirs = getters.getDirectories.slice()
		const dir = dirs.find(i => i['id'] === payload['data']['id'])
		if (dir) {
			await dashboardService.updateDirectory(
				dir,
				payload['callback'],
				payload['error']
			)
		}
	},
	async insertDirectory({commit, getters}, payload) {
		await dashboardService.insertDirectory(
			payload['data'],
			() => {
				const dirs = getters.getDirectories.slice()
				dirs.push(payload['data'])
				commit('setDirectories', dirs)
				payload['callback']()
			},
			payload['error']
		)
	},
	async insertEntry({commit, getters}, payload) {
		await dashboardService.insertEntry(
			payload['data'],
			() => {
				const entries = getters.getEntries.slice()
				entries.push(payload['data'])
				commit('setEntries', entries)
				payload['callback']()
			},
			payload['error']
		)
	},
	async updateEntry({getters}, payload) {
		const entries = getters.getEntries.slice()
		const entry = entries.find(i => i['id'] === payload['data']['id'])
		if (entry) {
			await dashboardService.updateEntry(
				entry,
				payload['callback'],
				payload['error']
			)
		}
	},
	async deleteEntry({commit, getters}, payload) {
		const data = payload['data']['item']['isDirectory'] ? getters.getDirectories.slice() : getters.getEntries.slice()
		const item = data.find(i => i['id'] === payload['data']['item']['id'])
		if (item) {
			await dashboardService.deleteEntry(
				item,
				item['isDirectory'],
				() => {
					let i = 0
					while (i < data.length) {
						if (data[i]['id'] === item['id']) {
							data.splice(i, 1)
							break
						}
						i++
					}
					commit(item['isDirectory'] ? 'setDirectories' : 'setEntries', data)
					payload['callback']()
				},
				payload['error']
			)
		}
	},
	// eslint-disable-next-line no-unused-vars
	async generateKey({commit}, payload) {
		payload['onSuccess'](generate(payload['isSimple'], payload['length']))
	},

	updateStoredDirName({commit}, payload) {
		commit('updateStoredDirName', payload)
	},
	updateStoredDirParent({commit}, payload) {
		commit('updateStoredDirParent', payload)
	},
	updateStoredEntryParent({commit}, payload) {
		commit('updateStoredEntryParent', payload)
	},
	logout() {
		dashboardService.logout()
	}
}

export default {
	namespaced: true,
	actions,
	state,
	getters,
	mutations
}