import Vue from 'vue'
import VueCookies from 'vue-cookies'

Vue.use(VueCookies)

const AUTH_DATA = 'Data'

const parseCookie = (cookieValue) => {
	switch (cookieValue) {
		case 'cookie-type-0': return '0s'; // No cookie value
		case 'cookie-type-1': return '1h';
		case 'cookie-type-2': return '6h';
		case 'cookie-type-3': return '24h';
	}
}

const setCookie = (cookie) => {
	const expireTime = parseCookie(cookie.time)
	Vue.$cookies.set(AUTH_DATA, {
		email: cookie.email,
		pwd: cookie.pwd
	}, expireTime)
}

const getCookieData = () => {
	return Vue.$cookies.get(AUTH_DATA)
}

const isAutoLogin = () => !!getCookieData()

export {
	setCookie,
	isAutoLogin,
	getCookieData
}